import React from 'react'
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect
} from "react-router-dom";
import { v4 as uuidV4 } from 'uuid'
import Main from './Main';


function App() {
  return (
    <Router>
      <div>Hello There!</div>
      <Switch>
        <Route path="/" exact>
          <Redirect to={`/${uuidV4()}`}></Redirect>
        </Route>
        <Route path='/:id'>
          <Main></Main>
        </Route>
      </Switch>
    </Router>


  );

}

export default App;
